import React, { useState } from 'react';
import './App.css';
import { Navbar, Nav, NavDropdown, Container, Button } from 'react-bootstrap';
import Product from './components/Product';
import Data from './data';
import { BrowserRouter, Link, Route, Switch } from 'react-router-dom'
import Detail from './components/Detail';
import axios from 'axios';

function App() {
  const [data, setData] = useState(Data)
  const [ajaxData, setAjaxData] = useState([])
  const [stock, setStock] = useState([10, 11, 12])
  
  return (
    <BrowserRouter>
      <div className="App">
        <Navbar bg="light" expand="lg">
          <Container>
            <Navbar.Brand href="#home">React-Bootstrap</Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="me-auto">
                <Nav.Link as={Link} to="/">Home</Nav.Link>
                <Nav.Link as={Link} to="/detail/0">Detail1</Nav.Link>
                <Nav.Link as={Link} to="/detail/1">Detail2</Nav.Link>
                <Nav.Link as={Link} to="/detail/2">Detail3</Nav.Link>
                <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                  <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                  <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                  <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                  <NavDropdown.Divider />
                  <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                </NavDropdown>
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>
        <Switch>
          <Route exact path="/">
            <div className="jumbotron">
              <div className="header">
                <h1>Hello, Shop</h1>
                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Alias ab, facere fugiat ex ea nostrum dignissimos consequatur dolores facilis beatae nam numquam commodi temporibus sapiente quae quam architecto neque vero.</p>
                <Button variant="primary">Primary</Button>{' '}
              </div>
            </div>
            <div className="container">
              <div className="row">
                {
                  data.map((el, i) => {
                    return (
                      <>
                        <Product prop={ el } key={i}/>
                      </>
                    )
                  })
                }
              </div>
              <button className="btn btn-primary" onClick={ () => {

                axios.get('https://codingapple1.github.io/shop/data2.json')
                .then((result) => {
                  const ad = result.data
                  setData([...data, ...ad])
                })
                .catch(() => {
                  console.log("실패했어요")
                })

              } }>더보기</button>
            </div>
          </Route>
          {/* <Route path="/detail1"><Detail data={ Data[0] }/></Route>
          <Route path="/detail2"><Detail data={ Data[1] }/></Route>
          <Route path="/detail3"><Detail data={ Data[2] }/></Route> */}
          <Route path="/detail/:id"><Detail stock={ stock } setStock={ setStock } Data={ Data }/></Route>
        </Switch>
      </div>
    </BrowserRouter>
  );
}

export default App;
